"use client"

import { useState } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { X } from "lucide-react"
import Image from "next/image"

export default function TestResults() {
  const [activeTest, setActiveTest] = useState<"duolingo" | "toefl" | null>(null)

  const openModal = (test: "duolingo" | "toefl") => {
    setActiveTest(test)
  }

  const closeModal = () => {
    setActiveTest(null)
  }

  return (
    <section id="results" className="py-16 px-4 mb-20">
      <div className="max-w-4xl mx-auto">
        <h2 className="text-4xl sm:text-5xl font-bold text-center mb-16 bg-gradient-to-r from-blue-600 to-purple-600 dark:from-blue-400 dark:to-purple-400 bg-clip-text text-transparent">
          Результаты наших студентов
        </h2>
        <div className="flex flex-col sm:flex-row justify-center items-center space-y-6 sm:space-y-0 sm:space-x-6">
          <TestCard
            title="Upload"
            src="../public/upload.png"
            color="bg-green-100 dark:bg-green-900"
            onClick={() => openModal("duolingo")}
          >
            <p className="text-sm text-gray-700 dark:text-gray-300 text-center">
              Завантажте власні тендерні файли ProZorro
            </p>
          </TestCard>

          <TestCard
            title="TOEFL"
            src="https://muhammadov-q.github.io/tender-UI/toefl.jpg"
            color="bg-blue-100 dark:bg-blue-900"
            onClick={() => openModal("toefl")}
          >
            <p className="text-sm text-gray-700 dark:text-gray-300 text-center">
              Вставте посилання на тендер
            </p>
          </TestCard>

        </div>
      </div>

      <AnimatePresence>
        {activeTest && (
          <motion.div
            key="modal"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-70 p-4"
            onClick={closeModal}
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              transition={{ duration: 0.3 }}
              className="bg-white dark:bg-gray-800 rounded-xl p-6 w-full max-w-md shadow-lg relative"
              onClick={(e) => e.stopPropagation()}
            >
              <button
                onClick={closeModal}
                className="absolute top-3 right-3 text-gray-500 hover:text-red-500"
              >
                <X size={24} />
              </button>

              {activeTest === "duolingo" && (
                <>
                  <h3 className="text-2xl font-bold text-center mb-4">Завантажити файл (Duolingo)</h3>
                  <input
                    type="file"
                    className="w-full border border-gray-300 dark:border-gray-700 p-2 rounded"
                  />
                </>
              )}

              {activeTest === "toefl" && (
                <>
                  <h3 className="text-2xl font-bold text-center mb-4">Вставте посилання (TOEFL)</h3>
                  <input
                    type="text"
                    placeholder="https://..."
                    className="w-full border border-gray-300 dark:border-gray-700 p-2 rounded"
                  />
                </>
              )}
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  )
}

function TestCard({
  title,
  src,
  color,
  onClick,
  children,
}: {
  title: string
  src: string
  color: string
  onClick: () => void
  children?: React.ReactNode
}) {
  return (
    <motion.div
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
      className={`rounded-lg shadow-lg p-6 cursor-pointer w-full sm:w-64 flex flex-col items-center transition-all duration-300 hover:shadow-xl ${color}`}
      onClick={onClick}
    >
      <div className="bg-white dark:bg-gray-800 rounded-full p-4 mb-4 shadow-md">
        <Image src={src || "/placeholder.svg"} alt={`${title} icon`} width={64} height={64} className="rounded-full" />
      </div>
      <h3 className="text-xl font-semibold text-gray-800 dark:text-white mb-2 text-center">{title}</h3>
      {children}
    </motion.div>
  )
}